import { h } from "preact";
import { useCallback, useContext, useEffect, useRef } from "preact/hooks";
import { SortableCard } from "../../sortable_card/sortableCard";
import { DeleteIcon } from "./deleteIcon";
import pStyle from "./paragraph.css";
import cardStyle from "../../card/card.css";
import { ReorderIcon } from "./reorderIcon";
import {
  deleteParagraph,
  EditorAction,
  EditorContext,
  endMove,
  moveParagraph,
  startMoving,
  updateParagraphText,
} from "../editorState";

interface ParagraphProps {
  id: string;
  index: number;
}

export function Paragraph(props: ParagraphProps) {
  const { state, dispatch } = useContext(EditorContext);
  const p = state.paragraphs[props.id];
  if (!p) {
    return null;
  }
  const bindDispatch = useCallback(function <
    A extends unknown[],
    B extends EditorAction
  >(fn: (...args: A) => B) {
    return useCallback((...args: A) => {
      dispatch(fn(...args));
    }, []);
  },
  []);
  return (
    <ParagraphPresenter
      id={props.id}
      index={props.index}
      text={p.text}
      isSorting={state.isSorting}
      isDragging={state.draggingIndex === props.index}
      focus={props.index === state.lastAdded}
      onChange={bindDispatch(updateParagraphText)}
      onDelete={bindDispatch(deleteParagraph)}
      onMoveStart={bindDispatch(startMoving)}
      onMove={bindDispatch(moveParagraph)}
      onMoveEnd={bindDispatch(endMove)}
    />
  );
}

interface ParagraphPresenterProps {
  id: string;
  index: number;
  text: string;
  isSorting: boolean;
  isDragging: boolean;
  focus: boolean;
  onChange?(text: string, id: string): void;
  onDelete?(id: string): void;
  onMoveStart?(index: number): void;
  onMove?(dragIndex: number, hoverIndex: number): void;
  onMoveEnd?(): void;
}

export function ParagraphPresenter(props: ParagraphPresenterProps) {
  const onChange = useCallback(
    (e: Event) => {
      props.onChange?.((e.currentTarget as HTMLInputElement).value, props.id);
    },
    [props.onChange, props.id]
  );
  const onDelete = useCallback(
    (e: Event) => {
      e.preventDefault();
      props.onDelete?.(props.id);
    },
    [props.id]
  );
  const textArea = useRef<HTMLTextAreaElement | null>(null);
  useEffect(() => {
    if (props.focus) {
      textArea.current?.focus();
    }
  }, [props.focus]);
  return (
    <li
      className={`${cardStyle.card} ${pStyle.paragraph}`}
      data-is-editing={props.isSorting ? "true" : "false"}
      data-is-dragging={props.isDragging ? "true" : "false"}
    >
      <SortableCard
        index={props.index}
        className={`${pStyle.move} ${pStyle["edit-form"]}`}
        onMovieStart={props.onMoveStart}
        onMove={props.onMove}
        onMoveEnd={props.onMoveEnd}
      >
        <ReorderIcon />
      </SortableCard>
      <div className={pStyle.textarea}>
        <textarea ref={textArea} value={props.text} onKeyUp={onChange} />
        <div className={pStyle.count}>
          <span>{props.text.length}</span>
        </div>
      </div>
      <div className={`${pStyle.delete} ${pStyle["edit-form"]}`}>
        <button onClick={onDelete}>
          <DeleteIcon />
        </button>
      </div>
    </li>
  );
}
