import { ComponentChildren, createContext, h } from "preact";
import { useCallback, useContext, useRef } from "preact/hooks";

interface SortableCardProps {
  index: number;
  onMovieStart?: (index: number) => void;
  onMove?: (dragIndex: number, dropIndex: number) => void;
  onMoveEnd?: () => void;
  className?: string;
  children?: ComponentChildren;
}

type DragInfo = {
  data: {
    index: number;
  } | null;
};
const DragContext = createContext<DragInfo>({ data: null });

export function SortableCard(props: SortableCardProps) {
  const ref = useRef<HTMLDivElement | null>(null);
  //const [isDragging, setDraggingState] = useState(false);
  const dragContext = useContext(DragContext);
  const onDragStart = useCallback(
    (e: DragEvent) => {
      const dataTransfer = e.dataTransfer;
      if (!dataTransfer) {
        throw new Error();
      }
      dataTransfer.dropEffect = "move";
      dataTransfer.effectAllowed = "move";
      dragContext.data = { index: props.index };
      props.onMovieStart?.(props.index);
    },
    [props.index]
  );
  const onDragEnd = useCallback(() => {
    props.onMoveEnd?.();
  }, [props.onMoveEnd]);
  const onDrag = useCallback(
    (e: DragEvent) => {
      if (!ref.current || !e.dataTransfer) {
        return;
      }
      if (!dragContext.data) {
        return;
      }
      const dragIndex = dragContext.data.index;
      const hoverIndex = props.index;
      // Don't replace items with themselves
      if (dragIndex === hoverIndex) {
        return;
      }
      // Determine rectangle on screen
      const hoverBoundingRect = ref.current?.getBoundingClientRect();
      // Get vertical middle
      const hoverMiddleY =
        (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      // Determine mouse position
      const clientOffset = { x: e.clientX, y: e.clientY };
      // Get pixels to the top
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      // Only perform the move when the mouse has crossed half of the items height
      // When dragging downwards, only move when the cursor is below 50%
      // When dragging upwards, only move when the cursor is above 50%
      // Dragging downwards
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return;
      }
      // Dragging upwards
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return;
      }
      // Time to actually perform the action
      props.onMove?.(dragIndex, hoverIndex);
      // Note: we're mutating the monitor item here!
      // Generally it's better to avoid mutations,
      // but it's good here for the sake of performance
      // to avoid expensive index searches.
      dragContext.data.index = hoverIndex;
    },
    [props.index]
  );
  return (
    <div
      ref={ref}
      className={props.className}
      draggable
      onDragStart={onDragStart}
      onDragOver={onDrag}
      onDragEnd={onDragEnd}
    >
      {props.children}
    </div>
  );
}
